/**
 * SwiperDod 轮播图指示点
 * @description 自定义轮播图指示点
 * @tutorial https://ext.dcloud.net.cn/plugin?id=284
 * @property {Number} current 当前指示点索引，必须是通过 `swiper` 的 `change` 事件获取到的 `e.detail.current`
 * @property {String} mode = [default|round|nav|indexes] 指示点的类型
 * 	@value defualt 默认指示点
 * 	@value round 圆形指示点
 * 	@value nav 条形指示点
 * 	@value indexes 索引指示点
 * @property {String} field mode 为 nav 时，显示的内容字段（mode = nav 时必填）
 * @property {String} info 轮播图的数据，通过数组长度决定指示点个数
 * @property {Object} dotsStyles 指示点样式
 * @event {Function} clickItem 组件触发点击事件时触发，e={currentIndex}
 */

export default {
	name: 'UniSwiperDot',
	props: {
		info: {
			type: Array,
			default () {
				return []
			}
		},
		current: {
			type: Number,
			default: 0
		},
		dotsStyles: {
			type: Object,
			default () {
				return {}
			}
		},
		// 类型 ：default(默认) indexes long nav
		mode: {
			type: String,
			default: 'default'
		},
		// 只在 nav 模式下生效，变量名称
		field: {
			type: String,
			default: ''
		}
	},
	data() {
		return {
			dots: {
				width: 8,
				height: 8,
				bottom: 10,
				color: '#fff',
				backgroundColor: 'rgba(0, 0, 0, .3)',
				border: '1px rgba(0, 0, 0, .3) solid',
				selectedBackgroundColor: '#333',
				selectedBorder: '1px rgba(0, 0, 0, .9) solid'
			}
		}
	},
	watch: {
		dotsStyles(newVal) {
			this.dots = Object.assign(this.dots, this.dotsStyles)
		},
		mode(newVal) {
			if (newVal === 'indexes') {
				this.dots.width = 20
				this.dots.height = 20
			} else {
				this.dots.width = 8
				this.dots.height = 8
			}
		}

	},
	created() {
		if (this.mode === 'indexes') {
			this.dots.width = 20
			this.dots.height = 20
		}
		this.dots = Object.assign(this.dots, this.dotsStyles)
	}
}
